import { Box } from "@mui/material";
import { Helmet } from "react-helmet-async";
import MyStepper from "../components/common/MyStepper";
import CustomCardsList from "../components/common/CustomCardsList";
import workSamplesImage from "../assets/images/workSamples.jpg";

const Records = ({ helmetTitle }) => {
  const steps = [
    {
      title: " ",
      info: "             1401.",
    },
    {
      title: "  ",
      info: "      (   )    90% !",
    },
    {
      title: "  ICT",
      info: "      ICT       1402  ",
    },
  ];

  const workSamplesList = [
    {
      img: workSamplesImage,
      title: "",
      info: "  SPA       .",
      link: "",
    },
    {
      img: workSamplesImage,
      title: "Todo List",
      info: "       .",
      link: "",
    },
  ];

  return (
    <Box
      sx={{
        height: "100%",
        width: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignContent: "center",
      }}
    >
      <Helmet>
        <title>{helmetTitle}</title>
      </Helmet>
      <Box sx={{ m: 6, mt: 12 }}>
        <MyStepper label="  " steps={steps} />
        <CustomCardsList title=" " cardsList={workSamplesList} />
      </Box>
    </Box>
  );
};

export default Records;
