import { Helmet } from "react-helmet-async";
import { Box, Typography } from "@mui/material";

import Grid from "@mui/material/Unstable_Grid2";
import Info from "../components/common/Info";

const mainInfo = [
  { label: "", name: "" },
  { label: " ", name: "" },
  { label: "", name: "24" },
  { label: " ", name: "" },
];

const PersonalInfo = ({ helmetTitle }) => {
  const personalInfsoData = {
    intrested:
      "             !             .           .                      .             .              !           .        .                .                    !                .            INFJ  INTJ .",
    books:
      "               .                    .                  .           .            .                                                                          ",
  };

  const personalInfoData = [
    {
      text: "             !             .           .                      .             .              !           .        .                .                    !                .            INFJ  INTJ .",
    },
    {
      text: "               .                    .                  .           .            .",
      booksList: [
        "       ",
        "     ",
        "    ",
        "           ",
        "     ",
        "     ",
        "        ",
        "     ",
        " ",
        " ",
        "       ",
        "    ",
      ],
    },
    {
      text: "            !                .           .          :",
      movies: [
        "  ",
        "  ( )",
        "  (Leon The Professional)",
        "   (The Shawshank Redemption)",
        "  (The Dark Knight)",
        " (Interstellar)",
        " (Breaking bad)",
        "  (Three kingdom)",
        "  (Game Of Throns)",
        "   (Extraordinary Attorney Woo)",
        " Friends",
        "   (The Adventures of Sherlock Holmes)",
      ],
    },
  ];

  return (
    <Box
      sx={{
        height: "100%",
        width: "100%",
        direction: "ltr",
        overflowY: "scroll",
      }}
    >
      <Helmet>
        <title>{helmetTitle}</title>
      </Helmet>

      <Grid container sx={{ m: 4, mb: 6 }}>
        {mainInfo.map((info) => (
          <Info label={info.label} name={info.name} />
        ))}
      </Grid>

      <Box sx={{ m: 4 }}>
        <Typography color="white" variant="h3">
           
        </Typography>
        <Typography
          color="white"
          variant="body2"
          sx={{ textAlign: "justify", m: 3 }}
        >
          {personalInfoData[0].text}
        </Typography>
      </Box>
      <Box sx={{ m: 4 }}>
        <Typography color="white" variant="h3">
            
        </Typography>
        <Typography
          color="white"
          variant="body2"
          sx={{ textAlign: "justify", m: 3 }}
        >
          {personalInfoData[1].text}
          <ul>
            {personalInfoData[1].booksList.map((book) => (
              <li>{book}</li>
            ))}
          </ul>
        </Typography>
      </Box>
      <Box sx={{ m: 4 }}>
        <Typography color="white" variant="h3">
              
        </Typography>
        <Typography
          color="white"
          variant="body2"
          sx={{ textAlign: "justify", m: 3 }}
        >
          {personalInfoData[2].text}
          <ul>
            {personalInfoData[2].movies.map((book) => (
              <li>{book}</li>
            ))}
          </ul>
        </Typography>
      </Box>
    </Box>
  );
};

export default PersonalInfo;
